let timeout;
const Settings = ({ location }) => {
  const settings = useSelector((state) => state.settings);
  const [state, setState] = useState({});
  const [mounted, setMounted] = useState(false);
  const [touched, setTouched] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const handleStateResult = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: {
          value: { ...prev[name].value, value: value },
          validity: validity,
        },
      };
    });
  };

  const fetchSettings = useCallback(async () => {
    try {
      const sets = await dispatch(actions.getSettings());

      setState(initialState(sets));
    } finally {
      setMounted(true);
    }
  }, [dispatch]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchSettings();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchSettings]);

  const handleSave = async () => {
    let body = {};

    for (const key in state) {
      body = { ...body, [key]: state[key].value };
      if (!state[key].validity) {
        setTouched(true);
        return dispatch(
          actions.fireSnake("error", t("common:complete_red_input"))
        );
      }
    }

    try {
      const newSets = await dispatch(actions.saveSettings(body));
      setState(initialState(newSets));
      dispatch(actions.fireSnake("success", t("common:saved_success")));
    } catch (err) {
      //
    }
  };

  return (
    <Fragment>
      <Typography variant="h4" className="py" align="center">
        {t("settings:general_settings")}
      </Typography>

      {!mounted ? (
        <Paper>
          <TableSkeleton row={4} col={2} />
        </Paper>
      ) : !settings.loading && settings.errors ? (
        <PageError
          text={settings.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchSettings}
        />
      ) : (
        <Fragment>
          <Grid container spacing={1}>
            {/* Generals */}
            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Typography variant="h4">{t("settings:site_info")}</Typography>
                <Box height={20} />
                <Input
                  label={t("settings:site_name") + " **"}
                  initialValue={state.site_name.value.value}
                  name="site_name"
                  getResult={handleStateResult}
                  required
                  min={2}
                  setTouched={touched}
                  serverError={isError(
                    settings.errors && settings.errors.data,
                    `site_name`
                  )}
                />

                <Box className="py">
                  <Typography variant="caption" className="py">
                    {t("settings:site_logo")}
                  </Typography>
                  <DropZone
                    getResult={(files, validity) =>
                      handleStateResult(files, validity, "site_logo")
                    }
                    initialImgs={state.site_logo.value.value}
                    multiple={false}
                    title={t("settings:site_logo_notice")}
                    touched={touched}
                    disabled={!!state.site_logo.value.value.length}
                    maxSize={50000}
                    required
                  />
                </Box>
                <Grid container spacing={1} className="py">
                  <Grid item md={6} sm={12} xs={12}>
                    <Input
                      label={t("settings:tap_auth")}
                      initialValue={state.tap_auth.value.value}
                      name="tap_auth"
                      getResult={handleStateResult}
                      // required
                      // min={2}
                      setTouched={touched}
                      serverError={isError(
                        settings.errors && settings.errors.data,
                        `tap_auth`
                      )}
                    />
                  </Grid>
                  <Grid item md={6} sm={12} xs={12}>
                    <Input
                      label="Google API"
                      initialValue={state.google_api.value.value}
                      name="google_api"
                      getResult={handleStateResult}
                      // required
                      // min={2}
                      setTouched={touched}
                      serverError={isError(
                        settings.errors && settings.errors.data,
                        `google_api`
                      )}
                    />
                  </Grid>
                </Grid>

                <Box className="flex align-items-center flex-justify-between py">
                  <Typography variant="caption" className="py">
                    {t("settings:tax_status")}
                  </Typography>
                  <IosSwitch
                    name="tax_status"
                    state={state.tax_status.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
              </Paper>
            </Grid>

            {/* Paper 2 Payment + Points */}
            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Box height={5} />
                <Typography variant="h4">
                  {t("settings:payment_methods")}
                </Typography>
                <Box height={15} />
                <Box className="flex align-items-center flex-justify-between">
                  <Typography variant="caption" className="py">
                    {t("settings:cod_payment")}
                  </Typography>
                  <IosSwitch
                    name="cod"
                    state={state.cod.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                <Box className="flex align-items-center flex-justify-between">
                  <Typography variant="caption" className="py">
                    {t("settings:credit_payment")}
                  </Typography>
                  <IosSwitch
                    name="credit_card"
                    state={state.credit_card.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                <Box height={20} />
                <Typography variant="h4">
                  {t("settings:points_settings")}
                </Typography>
                <Box height={15} />
                <Box className="flex align-items-center flex-justify-between">
                  <Typography variant="caption" className="py">
                    {t("settings:points_status")}
                  </Typography>
                  <IosSwitch
                    name="points_status"
                    state={state.points_status.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                <Box hidden={!+state.points_status.value.value}>
                  <Box className="py">
                    <Input
                      label={t("settings:points_value")}
                      initialValue={state.points_value.value.value}
                      name="points_value"
                      getResult={handleStateResult}
                      decimal
                      setTouched={touched}
                      serverError={isError(
                        settings.errors && settings.errors.data,
                        `points_value`
                      )}
                      withIcon={
                        <Typography variant="h6" color="primary">
                          $
                        </Typography>
                      }
                    />
                  </Box>
                  <Box className="py">
                    <Input
                      label={t("settings:points_max_percentage")}
                      initialValue={state.points_max_percentage.value.value}
                      name="points_max_percentage"
                      getResult={handleStateResult}
                      decimal
                      setTouched={touched}
                      serverError={isError(
                        settings.errors && settings.errors.data,
                        `points_max_percentage`
                      )}
                      withIcon={
                        <Typography variant="h6" color="primary">
                          %
                        </Typography>
                      }
                    />
                  </Box>
                </Box>
                <Box height={12} />
              </Paper>
            </Grid>

            {/* Email Settings */}
            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Typography variant="h4">
                  {t("settings:email_settings")}
                </Typography>
                <Box height={15} />
                <Box className="center">
                  <Typography variant="caption" color="textSecondary">
                    {t("settings:email_settings_notice")}
                  </Typography>
                </Box>
                <Box className="py">
                  <Input
                    label={t("settings:email_host")}
                    initialValue={state.email_host.value.value}
                    name="email_host"
                    getResult={handleStateResult}
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `email_host`
                    )}
                  />
                </Box>
                <Box className="py">
                  <Input
                    label={t("settings:email_port")}
                    initialValue={state.email_port.value.value}
                    name="email_port"
                    getResult={handleStateResult}
                    decimal
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `email_port`
                    )}
                  />
                </Box>
                <Box className="py">
                  <Input
                    label={t("settings:email_user")}
                    initialValue={state.email_user.value.value}
                    name="email_user"
                    getResult={handleStateResult}
                    email
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `email_user`
                    )}
                  />
                </Box>
                <Box className="py">
                  <Input
                    label={t("settings:email_password")}
                    initialValue={state.email_password.value.value}
                    name="email_password"
                    getResult={handleStateResult}
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `email_password`
                    )}
                  />
                </Box>
              </Paper>
            </Grid>
          </Grid>
          {/* Submit */}
          <Box className="center py-1">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleSave}
              endIcon={settings.itemLoading && <ButtonSpinner />}
              startIcon={<SaveRoundedIcon />}
              disabled={settings.itemLoading}
              style={{ width: "80%" }}
            >
              <Typography variant="h4" className="py-1">
                {t("settings:save_settings")}
              </Typography>
            </Button>
          </Box>
        </Fragment>
      )}
    </Fragment>
  );
};

export default Settings;
