import React, { useState, useEffect } from "react";
import { useDropzone } from "react-dropzone";
import {
  makeStyles,
  Box,
  Badge,
  Collapse,
  IconButton,
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import { useTranslation } from "react-i18next";
import ClearIcon from "@material-ui/icons/Clear";

const DropZone = ({
  title,
  getResult,
  required,
  touched,
  initialImgs,
  multiple,
  disabled,
  maxSize,
}) => {
  const classes = useStyles();
  const [files, setFiles] = useState(initialImgs);
  const [err, setErr] = useState(null);
  const { t } = useTranslation();

  const {
    // acceptedFiles,
    getRootProps,
    getInputProps,
    isDragActive,
  } = useDropzone({
    accept: "image/*",
    multiple: multiple,
    maxSize: maxSize || 120000,
    onDropRejected: (_, a) => setErr(t("common:image_err")),
    onDrop: (acceptedFiles) => {
      setErr(null);
      setFiles((prev) => {
        return [
          ...prev,
          ...acceptedFiles.map((file) =>
            Object.assign(file, {
              preview: URL.createObjectURL(file),
              id: Math.random(),
            })
          ),
        ];
      });
    },
    disabled: disabled,
  });

  const handleRemove = (file) => {
    setFiles((prev) => prev.filter((p) => p.id !== file.id));
    URL.revokeObjectURL(file.preview);
  };

  const thumbs = files.map((file) => (
    <Badge
      key={file.name}
      badgeContent={
        <IconButton
          size="small"
          className={classes.primaryBG}
          onClick={() => handleRemove(file)}
        >
          <ClearIcon fontSize="small" className="text-light" />
        </IconButton>
      }
      anchorOrigin={{ horizontal: "left", vertical: "top" }}
    >
      <div className={classes.thumb}>
        <div className={classes.thumbInner}>
          <img src={file.preview} alt={file.name} className={classes.img} />
        </div>
      </div>
    </Badge>
  ));

  useEffect(() => {
    //Submit using useEffect
    getResult(files, required ? !!files.length : true, "image");
    // Make sure to revoke the data uris to avoid memory leaks
    return () => {
      files.forEach((file) => URL.revokeObjectURL(file.preview));
    };
    // eslint-disable-next-line
  }, [files]);

  useEffect(() => {
    if (required && touched && !files.length) {
      setErr(t("common:image_err_empty"));
    }
    // eslint-disable-next-line
  }, [touched, required]);

  return (
    <section className={classes.container}>
      <Collapse in={!!err}>
        <Alert
          severity="error"
          className="my"
          action={
            <IconButton size="small" onClick={() => setErr(null)}>
              <ClearIcon fontSize="small" />
            </IconButton>
          }
        >
          {err}
        </Alert>
      </Collapse>

      <Box className="flex flex-start flex-wrap">{thumbs}</Box>
      {!disabled && (
        <div
          {...getRootProps({
            className: `${classes.dropzone} ${
              isDragActive ? classes.active : ""
            } py-1 ${disabled ? classes.disabled : ""}`,
          })}
        >
          <input {...getInputProps()} />
          <p className="center">{title}</p>
        </div>
      )}
    </section>
  );
};

const useStyles = makeStyles((theme) => ({
  container: {
    border: "1px solid #ccc",
    borderRadius: 3,
    padding: 5,
  },
  dropzone: {
    border: "2px dashed #ccc",
    borderRadius: 3,
    outline: "none",
    cursor: "pointer",
    "&:focus": {
      border: "2px dashed",
      borderColor: theme.palette.primary.main,
    },
  },
  active: {
    border: "2px dashed",
    borderColor: theme.palette.primary.main,
    // background: theme.palette.primary.light,
  },
  disabled: {
    cursor: "not-allowed",
    background: "#dedede",
    color: "#b5b5b5",
  },
  thumb: {
    display: "inline-flex",
    borderRadius: 2,
    border: "1px solid #eaeaea",
    marginBottom: 8,
    marginRight: 8,
    width: 100,
    height: 100,
    padding: 4,
    boxSizing: "border-box",
  },
  thumbInner: {
    display: "flex",
    justifyContent: "center",
    minWidth: 0,
    overflow: "hidden",
  },
  img: {
    display: "block",
    width: "auto",
    height: "100%",
  },
  primaryBG: {
    background: theme.palette.primary.main,
    "&:hover": {
      background: theme.palette.primary.light,
    },
  },
}));

export default DropZone;
